import React from 'react';
import {
    Container,
    Row,
    Col,
    Card,
    CardImgOverlay,
    CardImg,
    CardBody,
    CardTitle,
    CardText,
} from 'reactstrap';
import NavbarContent from '../components/navigation/navbar';
import { Img } from 'react-image';
import myimg from '../images/blog-content/NUS.jpg';
import myimg2 from '../images/blog-content/hackathon1.jpg';
import { Helmet } from 'react-helmet';

const AboutUs = () => {
    return (
        <>
            <Helmet title={'iD8 - About'} />
            <NavbarContent />
            <Card
                style={{
                    alignItems: 'center',
                    maxHeight: '90vh',
                }}
            >
                <CardImgOverlay
                    style={{
                        justifyContent: 'center',
                        alignContent: 'center',
                        maxWidth: '80vw',
                    }}
                >
                    <CardTitle style={{ color: 'maroon' }} tag="h2">
                        Who are We - Our Founder's Story
                    </CardTitle>
                    <CardText style={{ fontSize: '30px', color: 'black' }}>
                        <p className="overlaywhite">
                            iD8 was founded on the idea that the gap between
                            skills attained in education and skills required in
                            industry are big. Our goal here is to reduce that
                            gap so that students are well prepared for the
                            challenges and excitement industry provides. We run
                            different projects tackling current problems with
                            technologies used in the current industry trends
                            with best practices provided by mentors and senior
                            students in the university.
                        </p>
                    </CardText>
                </CardImgOverlay>
            </Card>
        </>
    );
};

export default AboutUs;
